import React, { useState, useEffect } from 'react';
import FormValidate from './FormValidate';

const Form = () => {
    const [inputValues, setInputValues] = useState({
        username: '',
        email: '',
        password: '',
        password2: ''
    })

    const [errors, setErrors] = useState({});
    
    const [isSubmitting, setIsSubmitting] = useState(false);

    const [checkbox, setCheckbox] = useState(false);

    const [sucessMessage, setSucessMessage] = useState(false);

    const handleSubmit = e => {
        e.preventDefault();

        setErrors(FormValidate(inputValues));
        setIsSubmitting(true);
    }

    const handleChanges = e => {
        const {name, value} = e.target;

        setInputValues({
            ...inputValues,
            [name]: value
        })
    }

    
    useEffect(() => {
        if(Object.keys(errors).length === 0 && isSubmitting === true && checkbox === true){
            setInputValues({
                username: '',
                email: '',
                password: '',
                password2: ''
            })

            setSucessMessage(true);
            setCheckbox(false);
            setIsSubmitting(false);
            
            document.querySelector('#form-validate').reset();

        }
    }, [errors]);

    return (    
        <form onSubmit={handleSubmit} autoComplete="off" id="form-validate">
            <div className={sucessMessage ? 'modal active' : 'modal'}> 
                <h1>
                Register  with sucess!
                <span onClick={() => setSucessMessage(false)}>X</span>
                </h1>
            </div>
                <input 
                    type="text" 
                    name="username" 
                    id="username" 
                    className="username" 
                    placeholder="Username"
                    onChange={handleChanges}                        
                    />
                {errors.name && <p>{errors.name}</p>}

                <input 
                    type="email" 
                    name="email" 
                    id="email" 
                    className="email" 
                    placeholder="Email"
                    onChange={handleChanges}                        
                    />
                {errors.email && <p>{errors.email}</p>}

                <input 
                    type="password" 
                    name="password" 
                    id="password" 
                    className="password" 
                    placeholder="Password"
                    onChange={handleChanges}                        
                    />
                {errors.password && <p>{errors.password}</p>}

                <input 
                    type="password" 
                    name="password2" 
                    id="password2" 
                    className="password2" 
                    placeholder="Confirm password"
                    onChange={handleChanges}                        
                    />
                {errors.password2 && <p>{errors.password2}</p>}
            
            <div className="condition-use">
                <div className="checkbox-input">
                    <input type="checkbox" id="checkbox" onClick={() => setCheckbox(!checkbox)}/>
                    <label htmlFor="checkbox"> I agree with <i>website</i> terms of uses</label>
                    {checkbox ||
                    <small>'You need to agree with terms of use to register'</small>
                    }
                </div>
            <button type="submit">Sign up</button>
            </div>
            <div className="login">
            <p>Already have an account?</p>
            <a href="#">Login here</a>
            </div>
        </form>
    )
}

export default Form;